<template>
  <div>

    <!--  -->
    <div class="search-header">
      <!--  -->
      <van-icon name="arrow-left" color="white" size="0.48rem" class="goback" @click="$router.back()"/>
      <!--  -->
      <van-search v-fofo v-model="kw" @input="input" @search="searchResult"
       placeholder="" background-colr="#1edada;" shape="round"
      />
    </div>

    <!--  -->
    <div class="sugg-list" v-if="kw.length !== 0">
      <div class="sugg-item" v-for="(suggest, index) in suggestList"
        :key="index" v-html="highlightSuggest(suggest,kw)" @click="suggestResult(suggest)">
      </div>
    </div>

    <!--  -->
    <div class="search-history" v-else>
      <!--  -->
      <van-cell title="">
        <!--  right-icon  -->
        <template #right-icon>
          <van-icon name="delete" class="search-icon" @click="historyClear"/>
        </template>
      </van-cell>

      <!--  -->
      <div class="history-list">
        <span v-for="(history, index) in historyList" :key="index" @click="historyResult(history)"
          class="history-item">{{history}}
        </span>
      </div>
    </div>

  </div>
</template>

<script>
  import { getSuggeListAPI } from '../../api'

  export default {
    name: 'Search',
    data () {
      return {
        kw: '', // 
        timer: null, // 
        suggestList: [], // 
        historyList: JSON.parse(localStorage.getItem('his'))  || [] // 
      }
    },
    watch: {
      // ES62( Array, Object), (: Set Map)
      // Set: value ()
      // : , , , Set
      historyList: {
        deep: true,
        handler() {
          const hisrorySet = new Set(this.historyList)
          // Set ->  -> Array
          const hisroryArr = Array.from(hisrorySet)
          localStorage.setItem('his', JSON.stringify(hisroryArr))
        },
      }
    },
    methods: {
      //  - 
      input () {
        // : , , 
        clearTimeout(this.timer)
        if (this.kw.length === 0) {
          this.suggestList = []
        } else {
          this.timer = setTimeout(async () => {
            const res = await getSuggeListAPI({
              keywords: this.kw
            })
            this.suggestList = res.data.data.options
          }, 300)
        }
      },
      //   originStr  match 
      highlightSuggest(originStr, match) {
        // .replace() -> 
        // .replaceAll() -> 
        const reg = new RegExp(match, 'ig') // i, g
        return originStr.replace(reg, (match) => {
          return `<span style="color: red">${match}</span>`
        })
      },
      // : , watch, , , 
      //    
      searchResult() {
       if (this.kw.length > 0) {
         this.historyList.push(this.kw)
         setTimeout(() => {
           this.$router.push({
             path: `/search_result/${this.kw}`
           })
         },0)
       }
      },
      //  
      suggestResult (suggest) {
        this.historyList.push(suggest)
        setTimeout(() => {
          this.$router.push({
            path: `/search_result/${suggest}`
          })
        },0)
      },
      historyResult(history) {
        this.historyList.push(history)
        setTimeout(() => {
          this.$router.push({
            path: `/search_result/${history}`
          })
        },0)
      },
      // 
      historyClear () {
        this.historyList = []
      }
    },
  }
</script>

<style scoped lang="less">
  .search-header {
    height: 46px;
    display: flex;
    align-items: center;
    background-color: #1edada;
    overflow: hidden;

    /**/

    .goback {
      padding-left: 14px;
    }

    /**/

    .van-search {
      flex: 1;
      background-color: #1edada;
    }
  }

  /* */
  .sugg-list {
    .sugg-item {
      padding: 0 15px;
      border-bottom: 1px solid #1edada;
      font-size: 14px;
      line-height: 50px;
      // 
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  /** */
  .search-icon {
    font-size: 16px;
    line-height: inherit;
  }

  .history-list {
    padding: 0 10px;
    .history-item {
      display: inline-block;
      font-size: 12px;
      padding: 8px 14px;
      background-color: #efefef;
      margin: 10px 8px 0px 8px;
      border-radius: 10px;
    }
  }
</style>
